import React,{useState} from 'react';
import Table from './tablas/Table'
import Form from './Form'



 class Reloj extends React.Component {
     constructor(props){
         super(props);
         this.state = {date: new Date()}
     }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(), 1000);
        
    }

    tick() {
        this.setState({
            date: new Date()
        });
    }
    
     render(){
         return(
             <div>
                 <h2> La hora es: {this.state.date.toLocaleTimeString()}</h2>
             </div>
         )
     }
 }


const Section = (props) => {
    
    
    
    // function handleSubmitUser(user) {
    //     setData([...data,user])
    // }

    const [dataInput, setDataInput] = useState([]);
    
    function recibirDatos(user) {
        setDataInput([...dataInput, user]);
    }
    




    return (
        <div >
            <section className="section-parrafo">
            
            <div className="contenedor-formulario">
            <Form recibirDatos={recibirDatos} />
            
            <Table dataInput={dataInput}/>
            </div>

            {<Reloj/> }
            



            </section>



            
        </div>
    );
};

export default Section;