import React from 'react';

const Tbody = (props) => { 
    // le estoy panado prop data input


    const filas = props.dataInput.map((fila)=>{
        // aca mapeo para pasar los datos y creo unos td retornando lo que me devuelve ek dataInput
        return(
            <tr>
                <td>{fila.nombre}</td>
                <td>{fila.oficio}</td>
                <td><button className="botonDelete" >Delete</button></td>
                </tr>
        )
    })


    return (
        <div className="tbody">
            <tbody>
               {filas}
            </tbody>
            
        </div>
    );
};

export default Tbody;