import React, { useState } from 'react';

const Form = (props) => {
    //     ''
    const dataUser = {
        nombre: "",
        oficio: "",
      };


    const [datos, setDatos] = useState(dataUser);
    
    const handleSubmit = (e) => {
        e.preventDefault();
        props.recibirDatos(datos);

      };

    const handleChange = (e) => {
        setDatos({
          ...datos,
          [e.target.name]: e.target.value,
          
         
          
        });
      };
    
    

    return ( <form onSubmit={handleSubmit} className="formulario">
        <div>
        <label>Nombre:</label>
        <input type="text"  value={datos.nombre} name="nombre" onChange={handleChange}/>
        </div>
        <div>
        <label>Oficio:</label>
        <input type="text" value={datos.oficio} name="oficio" onChange={handleChange}/>
        </div>
        <input className="boton" type="submit"/>
    
    </form> 

    );

    

};

export default Form;